import React from 'react';
import { NavLink } from 'react-router-dom';

function NavOverlay() {
    return (
        <nav>
            <ul>
                <h4><NavLink to="/about" activeClassName="navActive">Over</NavLink></h4>
                <h4><NavLink to='/cases' activeClassName="navActive">Cases</NavLink></h4>
                <h4><NavLink to="/contact" activeClassName="navActive">Contact</NavLink></h4>
            </ul>
        </nav>
    );
}

export default NavOverlay;