import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';

import NavOverlay from './navOverlay'

function Header() {
  const [showMenu, setShowMenu] = useState(false)

  let overlay

  if (showMenu) {
    // alert("tadaa");
      overlay =
          <div onClick={() => setShowMenu(!showMenu)}>
              <NavOverlay />
          </div>
  }

  function test(){
    alert("tadaa");
  }

  return (
    <header>
      <div className='logoL'>
        <h1><Link to="/">Kylian Gieles</Link></h1>
      </div>
      <div className='logoS'>
        <h1><Link to="/">KG</Link></h1>
      </div>

      <ul classname="nav">
        <div>
          <li><NavLink to="/over" extact activeClassName="navActive">Over</NavLink></li>
        </div>
        <div>
          <li><NavLink to="/cases" extact activeClassName="navActive">Cases</NavLink></li>
        </div>
        <div>
          <li><NavLink to="/contact" extact activeClassName="navActive">Contact</NavLink></li>
        </div>
      </ul>

      {/* MOBILE NAV */}
      <div className="hamburgerIcon" onClick={test}>
      {/* <div className="hamburgerIcon" onClick={() => setShowMenu(!showMenu)}> */}
        <div class="bar1"></div>
        <div class="bar2"></div>
        <div class="bar3"></div>
      </div>
    </header>
  );
}

export default Header;